import { IProducts } from '@/types/card.type';
import Image from 'next/image';
export const Product = ({ product }: { product: IProducts }) => {
  return (
    <div>
      <div>
        <Image
          src={product.imageUrl}
          alt={product.name}
          width={50}
          height={50}
        />
        <h2>{product.name}</h2>
        <p>${product.price}</p>
      </div>
    </div>
  );
};
